@use "global" as *;

/* ========== Layout ========== */
.service__inner {
  margin: auto;
  padding: 0 rem(20);
  max-width: rem(600);
  width: 100%;

  @include mq(md) {
    padding: 0 rem(25);
    max-width: rem(1200);
  }
}

.service-swiper {
  position: relative;
}

/* Swiper */
// .service-swiper .swiper {
//   // padding-right: clamp(40px, 10vw, 160px);
// }

/* ========== Slides1s ========== */
.service-swiper .swiper-slide {
  width: 100vw; /*  */
  max-width: 720px;
  transform: scale(0.847);
  transition: transform 1s;
}
.service-swiper .swiper-slide-active {
  transform: scale(1);
}

/*  */
.service-swiper .swiper-slide img {
  width: 100%;
  aspect-ratio: 720 / 420;
  object-fit: cover;
}

/* controls */
.service-swiper .swiper-pagination {
  bottom: rem(-48);
}

/* bullets */
.service-swiper .swiper-pagination .swiper-pagination-bullet {
  border: 1px solid #333;
  background: #fff9ee;
}

/* ========== Controls ========== */
.service-swiper__controls {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center; /*  flex-end */
  align-items: center;
  gap: 1.5rem;
  margin-top: 2rem;
}

/* Swiperabsolute */
.service-swiper__controls .swiper-pagination {
  position: static !important;
  bottom: auto !important;
  left: auto !important;
  right: auto !important;
  width: auto !important;
  margin: 0 !important;
  transform: none !important;
  text-align: left; /*  */
}

/* fraction01  06 */
.service-swiper__controls .swiper-pagination.swiper-pagination-fraction {
  display: inline-flex !important;
  align-items: center !important;
  gap: .75rem !important;
  width: auto !important;
  font-feature-settings: "tnum";
}

/* JSrenderFraction */
.fraction-separator {
  display: inline-block;
  margin: 0 .75rem;
}

/*  */
.service-swiper__nav {
  display: flex;
  gap: 1rem;
}
.service-swiper__nav .nav {
  position: static;  /*  */
  transform: none;
  width: auto;
  height: auto;
  background: transparent;
  cursor: pointer;
}
.service-swiper__nav .nav svg {
  width: 51px;
  height: 50px;
  filter: drop-shadow(0 10px 30px rgba(0,0,0,.08));
}

